import { BaseModel, mergeObjects } from "@wolseley/common";
export class ItemDetail extends BaseModel {
    constructor(initialData) {
        super();
        this.ItemIdentifier = null;
        this.RequestedDateTime = new Date().toISOString();
        this.OrgInfoList = [{ OrgIdentifier: "1BL" }];
        this._required = [
            "ItemIdentifier",
            "RequestedDateTime",
            "OrgInfoList"
        ];
        const data = mergeObjects(this, initialData);
        Object.assign(this, data);
    }
}
