import { BaseModel, mergeObjects } from "@wolseley/common";
export class QuickAvailabilityFind extends BaseModel {
    constructor(initialData) {
        super();
        this.CallingInstance = "GOP";
        this.CallingModule = "GOP";
        this.RequestCreationDateTime = new Date().toISOString();
        this.ItemDetailList = [];
        this._required = [
            "CallingInstance",
            "CallingModule",
            "RequestCreationDateTime",
            "ItemDetailList"
        ];
        const data = mergeObjects(this, initialData);
        Object.assign(this, data);
    }
}
